import styled from '@emotion/styled';
import { Link, useNavigate } from 'react-router-dom';

import { Container } from '@/components/common/layouts/Container';
import { useAuth } from '@/provider/Auth';
import { getDynamicPath, RouterPath } from '@/routes/path';
import { useState } from 'react';
import { useServer } from '@/provider/ServerProvider';

export const Header = () => {
  const navigate = useNavigate();
  const authInfo = useAuth();
  const { serverURL, setServerURL } = useServer();

  const servers = [
    { name: '', baseURL: 'https://api.example.com' },
    { name: '', baseURL: 'https://api.example2.com' },
    { name: '', baseURL: 'https://api.example3.com' },
    { name: '', baseURL: 'https://api.example4.com' },
  ];

  const [selectedServer, setSelectedServer] = useState(
    serverURL || servers[0].baseURL
  );

  const handleServerChange = (event: React.ChangeEvent<HTMLSelectElement>) => {
    const selectedURL = event.target.value;
    setSelectedServer(selectedURL);
    setServerURL(selectedURL); //    URL  
  };

  const handleLogin = () => {
    navigate(getDynamicPath.login());
  };

  return (
    <Wrapper>
      <Container
        flexDirection='row'
        alignItems='center'
        justifyContent='space-between'
      >
        <Link to={RouterPath.home}>
          <Logo
            src='https://gift-s.kakaocdn.net/dn/gift/images/m640/pc_gift_logo.png'
            alt='  '
          />
        </Link>
        <RightWrapper>
          <select onChange={handleServerChange} value={selectedServer}>
            {servers.map((server) => (
              <option key={server.baseURL} value={server.baseURL}>
                {server.name}
              </option>
            ))}
          </select>
          {authInfo ? (
            <LinkButton onClick={() => navigate(RouterPath.myAccount)}>
               
            </LinkButton>
          ) : (
            <LinkButton onClick={handleLogin}></LinkButton>
          )}
        </RightWrapper>
      </Container>
    </Wrapper>
  );
};

export const HEADER_HEIGHT = '54px';

export const Wrapper = styled.header`
  position: fixed;
  z-index: 9999;
  width: 100%;
  max-width: 100vw;
  height: ${HEADER_HEIGHT};
  background-color: #fff;
  padding: 0 16px;
`;

const Logo = styled.img`
  height: ${HEADER_HEIGHT};
`;
const RightWrapper = styled.div`
  display: flex;
`;

const LinkButton = styled.p`
  align-items: center;
  font-size: 14px;
  color: #000;
  text-decoration: none;
  cursor: pointer;
  margin-left: 20px;
`;
